import { useQuery } from "react-query";
import { useMatch, useNavigate } from "react-router-dom";
// styles : import * as styled from "./Movie.style";
import { Loader } from "../../style/Elementary";
import {
  Wrapper,
  BackDrop,
  BDTitle,
  BDOverview,
  Slider,
  SliderTitle,
  SliderRow,
  SliderRowItem,
  SliderBtns,
  SliderBtn,
  Info,
  InfoDetail,
  Overlay,
  PosterImg,
} from "./Main.style";
import { AnimatePresence, motion, useViewportScroll } from "framer-motion";
import { IgetMoviesResult, movieNowPlaying } from "../../utility/api";
import { BtnNext, BtnPrev, makeImagePath } from "../../utility/utils";
import { useState } from "react";
import {
  BtnVariants,
  InfoVariants,
  SliderBDTitleVariants,
  SliderItemVariants,
  SliderVariants,
} from "./variants";

function Movie() {
  const offset = 6;
  const navigate = useNavigate();
  const MovieInfoMatch = useMatch("/movie/:movieId");
  const { scrollY } = useViewportScroll();
  const [isBack, setIsBack] = useState(false);
  const { data, isLoading } = useQuery<IgetMoviesResult>(
    ["movies", "nowPlaying"],
    movieNowPlaying
  );
  const [index, setIndex] = useState(0);
  const [leaving, setLeaving] = useState(false);
  const SliderNext = () => {
    if (data) {
      if (leaving) return;
      setIsBack(false);
      toggleLeaving();
      const totalMovies = data.results.length - 1;
      const maxIndex = Math.floor(totalMovies / offset) - 1;
      setIndex((prev) => (prev === maxIndex ? 0 : prev + 1));
    }
  };
  const SliderPrev = () => {
    if (data) {
      if (leaving) return;
      setIsBack(true);
      toggleLeaving();
      const totalMovies = data.results.length - 1;
      const maxIndex = Math.floor(totalMovies / offset) - 1;
      setIndex((prev) => (prev === 0 ? maxIndex : prev - 1));
    }
  };
  const toggleLeaving = () => {
    setLeaving((prev) => !prev);
  };
  const onItemClicked = (movieId: number) => {
    navigate(`/movie/${movieId}`);
  };
  const overayClick = () => {
    navigate(`/movie`);
  };
  const clickedMovie =
    MovieInfoMatch?.params.movieId &&
    data?.results.find(
      (movie) => movie.id + "" === MovieInfoMatch.params.movieId
    );
  return (
    <Wrapper>
      {isLoading ? (
        <Loader>Loading</Loader>
      ) : (
        <>
          <BackDrop
            bgphoto={makeImagePath(data?.results[0].backdrop_path || "")}
          >
            <BDTitle>{data?.results[0].title}</BDTitle>
            <BDOverview>{data?.results[0].overview}</BDOverview>
          </BackDrop>

          <Slider>
            <SliderTitle>Now Playing</SliderTitle>
            <AnimatePresence
              custom={isBack}
              initial={false}
              onExitComplete={toggleLeaving}
            >
              <SliderRow
                key={index}
                custom={isBack}
                variants={SliderVariants}
                initial="stop"
                animate="moving"
                exit="next"
                transition={{ duration: 0.7 }}
              >
                {data?.results
                  .slice(1)
                  .slice(offset * index, offset * index + offset)
                  .map((movie) => (
                    <SliderRowItem
                      key={movie.id}
                      layoutId={movie.id + ""}
                      variants={SliderItemVariants}
                      whileHover="hover"
                      initial="normal"
                      transition={{ type: "tween" }}
                      onClick={() => onItemClicked(movie.id)}
                      bgphoto={makeImagePath(movie.backdrop_path, "w500")}
                    >
                      <motion.p variants={SliderBDTitleVariants}>
                        {movie.title}
                      </motion.p>
                      <Info variants={InfoVariants}>{movie.title}</Info>
                    </SliderRowItem>
                  ))}
              </SliderRow>
            </AnimatePresence>
            <SliderBtns>
              <SliderBtn
                onClick={SliderPrev}
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 256 512"
              >
                <motion.path
                  variants={BtnVariants}
                  initial="normal"
                  whileHover="hover"
                  d={BtnPrev}
                />
              </SliderBtn>
              <SliderBtn
                onClick={SliderNext}
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 256 512"
              >
                <motion.path
                  variants={BtnVariants}
                  initial="normal"
                  whileHover="hover"
                  d={BtnNext}
                />
              </SliderBtn>
            </SliderBtns>
          </Slider>
          <AnimatePresence>
            {MovieInfoMatch ? (
              <>
                <Overlay
                  onClick={overayClick}
                  animate={{ opacity: 1 }}
                  exit={{ opacity: 0 }}
                />
                <InfoDetail
                  style={{ top: scrollY.get() + "10vh" }}
                  layoutId={MovieInfoMatch.params.movieId}
                >
                  {clickedMovie && (
                    <>
                      <PosterImg
                        src={`${makeImagePath(
                          clickedMovie.poster_path,
                          "original"
                        )}`}
                      />
                      <div>
                        <h1>{clickedMovie.title}</h1>
                        <p>{clickedMovie.overview}</p>
                      </div>
                    </>
                  )}
                </InfoDetail>
              </>
            ) : null}
          </AnimatePresence>
        </>
      )}
    </Wrapper>
  );
}

export default Movie;
