import { useQuery } from "react-query";
import { ITvOntheair, tvOntheair } from "../../utility/api";
import { Loader } from "../../style/Elementary";
import { useState } from "react";
import {
  BackDrop,
  BDOverview,
  BDTitle,
  Info,
  InfoDetail,
  Overlay,
  PosterImg,
  Slider,
  SliderBtn,
  SliderBtns,
  SliderRow,
  SliderRowItem,
  SliderTitle,
  Wrapper,
} from "./Main.style";
import { BtnNext, BtnPrev, makeImagePath } from "../../utility/utils";
import { motion, AnimatePresence, useViewportScroll } from "framer-motion";
import {
  BtnVariants,
  InfoVariants,
  SliderBDTitleVariants,
  SliderItemVariants,
  SliderVariants,
} from "./variants";
import { useMatch, useNavigate } from "react-router-dom";

function Tv() {
  const offset = 6;
  const [index, setIndex] = useState(0);
  const TVshowInfoMatch = useMatch("/tv/:tvId");
  const navigate = useNavigate();
  const { scrollY } = useViewportScroll();
  const { data, isLoading } = useQuery<ITvOntheair>(
    ["tv", "ontheair"],
    tvOntheair
  );
  const [isBack, setIsBack] = useState(false);
  const [leaving, setLeaving] = useState(false);
  const toggleLeaving = () => {
    setLeaving((prev) => !prev);
  };
  const SliderNext = () => {
    if (data) {
      if (leaving) return;
      setIsBack(false);
      toggleLeaving();
      const totalTv = data.results.length - 1;
      const maxIndex = Math.floor(totalTv / offset) - 1;
      setIndex((prev) => (prev === maxIndex ? 0 : prev + 1));
    }
  };
  const SliderPrev = () => {
    if (data) {
      if (leaving) return;
      setIsBack(true);
      toggleLeaving();
      const totalTv = data.results.length - 1;
      const maxIndex = Math.floor(totalTv / offset) - 1;
      setIndex((prev) => (prev === 0 ? maxIndex : prev - 1));
    }
  };
  const onClickTVShow = (tvId: number) => {
    navigate(`/tv/${tvId}`);
  };
  const overayClick = () => {
    navigate(`/tv`);
  };
  const clickedTVShow =
    TVshowInfoMatch?.params.tvId &&
    data?.results.find((tv) => tv.id + "" === TVshowInfoMatch?.params.tvId);
  return (
    <Wrapper>
      {isLoading ? (
        <Loader>Loading</Loader>
      ) : (
        <>
          <BackDrop
            bgphoto={makeImagePath(data?.results[0].backdrop_path || "")}
          >
            <BDTitle>{data?.results[0].name}</BDTitle>
            <BDOverview>{data?.results[0].overview}</BDOverview>
          </BackDrop>

          <Slider>
            <SliderTitle>On the Air</SliderTitle>
            <AnimatePresence
              custom={isBack}
              initial={false}
              onExitComplete={toggleLeaving}
            >
              <SliderRow
                key={index}
                custom={isBack}
                variants={SliderVariants}
                initial="stop"
                animate="moving"
                exit="next"
                transition={{ duration: 0.7 }}
              >
                {data?.results
                  .slice(1)
                  .slice(offset * index, offset * index + offset)
                  .map((tv) => (
                    <SliderRowItem
                      key={tv.id}
                      layoutId={tv.id + ""}
                      onClick={() => onClickTVShow(tv.id)}
                      bgphoto={makeImagePath(tv.backdrop_path, "w500")}
                      variants={SliderItemVariants}
                      whileHover="hover"
                      initial="normal"
                      transition={{ type: "tween" }}
                    >
                      <motion.p variants={SliderBDTitleVariants}>
                        {tv.name}
                      </motion.p>
                      <Info variants={InfoVariants}>{tv.name}</Info>
                    </SliderRowItem>
                  ))}
              </SliderRow>
            </AnimatePresence>
            <SliderBtns>
              <SliderBtn
                onClick={SliderPrev}
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 256 512"
              >
                <motion.path
                  variants={BtnVariants}
                  initial="normal"
                  whileHover="hover"
                  d={BtnPrev}
                />
              </SliderBtn>
              <SliderBtn
                onClick={SliderNext}
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 256 512"
              >
                <motion.path
                  variants={BtnVariants}
                  initial="normal"
                  whileHover="hover"
                  d={BtnNext}
                />
              </SliderBtn>
            </SliderBtns>
          </Slider>
          <AnimatePresence>
            {TVshowInfoMatch ? (
              <>
                <Overlay
                  onClick={overayClick}
                  animate={{ opacity: 1 }}
                  exit={{ opacity: 0 }}
                />
                <InfoDetail
                  style={{ top: scrollY.get() + "10vh" }}
                  layoutId={TVshowInfoMatch.params.tvId}
                >
                  {clickedTVShow && (
                    <>
                      <PosterImg
                        src={`${makeImagePath(
                          clickedTVShow.poster_path,
                          "original"
                        )}`}
                      />
                      <div>
                        <h1>{clickedTVShow.name}</h1>
                        <p>{clickedTVShow.overview}</p>
                      </div>
                    </>
                  )}
                </InfoDetail>
              </>
            ) : null}
          </AnimatePresence>
        </>
      )}
    </Wrapper>
  );
}

export default Tv;
